body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x:hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:-webkit-any-link {
  color: unset;
  cursor: pointer;
  text-decoration: none;
}



.category-container{
    width: 20%;
    float:left;
    margin: 2% 0;
}

@media (max-width: 1024px){
    .category-container{
        width: 30%;
    }
}


@media (max-width: 640px){
    .category-list-container{
        display:flex;
        flex-direction:column;
        align-items: flex-start !important;
    }

    .category-name{
        padding: 0 !important;
    }
}

@media (max-width: 500px){
    .category-checkbox{
        display:none !important;
    }

    .selected-category{
        border:3px solid #5D38DB;
        padding: 2px;
    }
}
.meals-container{
    width: 80%;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    float:left;
    margin: 2% 0;
    justify-content: center;
}

.meals-container a {
    margin: 0 5% 2% 5%;
    flex:1 1;
}

.fav-meals-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 2%;
    justify-content: flex-start;
}

.fav-meals-container a{
    margin: 2%;
}

@media (max-width: 1024px){
    .meals-container{
        width: 70%;
    }

    .meals-container a {
        flex:unset;
    }   
}

@media (max-width:768px) {
    .fav-meals-container{
        justify-content: center;
    }
}

@media (max-width:500px){
    .meals-container{
        width: 60%;
    }   
}
.fa.fa-heart.favMeal{
    color:red;
}

@media (max-width:500px){
    .meal-card{
        width:300px;
    }
}
.meal-detail-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2%;
}

.meal-detail-container .meal-header{
    font-size: 24px;
    font-weight: bold;
    margin: 1% 0;
}

.meal-detail-container .meal-img-container{
    width: 50%;
}

.meal-detail-container .meal-img-container img{
    width: 100%;
    height: auto;
}

.meal-detail-container .meal-instruction{
    width: 70%;
    margin: 1% 0;
}
